<template>
    <div data-app="true">
    
      <v-row >
        <v-col cols="6"
        sm="3" v-for="image in images"  :key="image.name" >
        <a download :href="image.fileUrl">
          <v-img :src="`data:image/png;base64,${image.thumbnail}`" alt="" />
        </a>

        </v-col>
      
      </v-row >

     

    </div>
  
</template>

<script>
export default {

    data() {
    return{
        images:{},
        CmdImages: {
        name: "camera.listFiles",
        parameters: {
            entryCount: 50,
            fileType: "all",
            maxThumbSize: 100,
        }},
       
} 
},
methods: {
    log(message) {
      this.$emit("log", message);
      console.log(message)
    },
   
    getImages(){
      if(window.test){
        this.images = window.sampleImages.results.entries
        console.log(this.images)

      }else{
      this.postData('http://192.168.43.1:6624/osc/commands/execute', this.CmdImages )
      .then((data) => {this.images = data.results.entries})
    }
      
    },
     postData(url, data = {}){
        return fetch(url, {
    method: 'POST', // *GET, POST, PUT, DELETE, etc.
    headers: {
      'Content-Type': 'application/json;charset=utf-8',
      'Cache-Control': 'no-cache',
      // 'Content-Type': 'application/x-www-form-urlencoded',
    },
    body: JSON.stringify(data) // body data type must match "Content-Type" header
  })      
      .then(response => response.json())
      .catch(err=>this.log(err))
    }



},
mounted() {
  setTimeout(() => {
    this.getImages()
  }, 200);
 

},
watch: {}
}
</script>