<template>
    <div>
     
  
    <SettingsPage @log="log"></SettingsPage>
    <GalleryPage @log="log"></GalleryPage>
<template>
    <v-snackbar  :style="{'margin-bottom':calcMargin(i)}" v-for="(s,i) in logs" :key="i" v-model="show" >
                {{ s }}
    </v-snackbar>
  </template>

</div>
  
</template>

<script>
import GalleryPage from './GalleryPage';
import SettingsPage from './SettingsPage';


export default {
  data() {
    return {
      logs: [],
      show: false
    }},
  methods: {
    log (value) {
      this.show=true; 
      this.logs.push(value)
      setTimeout(() => {
        this.logs.splice(0,1)
      }, 4000);
    },
    calcMargin(i) {
            return (i*50) + 'px'
        },

  },
 
  components: {
    GalleryPage,
    SettingsPage
  },
}
</script>