<template>
  <div data-app="true">
    <v-system-bar dark color="primary">
      <span>{{ settings.remainingPictures }} img</span>

      <v-spacer></v-spacer>
      <span>{{ settings.remainingVideoMinutes }}min</span>
      <v-spacer></v-spacer>
      <span>sleep:{{ settings.sleepDelay }}sec</span>
    </v-system-bar>
    <v-app-bar>
      <v-btn fab small @click="dialog = true">
        <v-icon>settings</v-icon>
      </v-btn>
      <v-spacer></v-spacer>
      <!-- <v-app-bar-nav-icon @click="dialog = true"> <v-icon>gear</v-icon></v-app-bar-nav-icon> -->

      <v-toolbar-title>

        <!-- <InfoPage></InfoPage> -->
      </v-toolbar-title>
      <v-btn fab small onclick="location.reload(true)">
        <v-icon>refresh</v-icon>
      </v-btn>
      <v-spacer></v-spacer>
      <v-btn fab small @click="takePicture">
        <v-icon>photo_camera</v-icon>
      </v-btn>
      <v-spacer></v-spacer>
      <v-btn fab small @click="record">
        <v-icon>videocam</v-icon>
      </v-btn>
      <v-spacer></v-spacer>
      <v-btn fab small @click="capture">
        <v-icon>timer</v-icon>
      </v-btn>

      <v-spacer></v-spacer>


      <v-progress-circular right :rotate="360" :value="settings._batteryLevel" color="teal">
        {{ settings._batteryLevel }}
      </v-progress-circular>



    </v-app-bar>

    <template>
      <div class="text-center">
        <v-dialog v-model="dialog" width="500">
          <v-card>
            <v-card-title class="text-h5 grey lighten-2">
              Settings
            </v-card-title>

            <v-card-text>
              <v-row align="center">
                <v-col cols="6">
                  <v-select v-model="settings.sleepDelay" :items="settings.sleepDelaySupport" label="sleep"
                    :value="settings.sleepDelay" @change="setOption('sleepDelay', $event)"></v-select>
                  <v-select v-model="settings._timer" :items="settings._timerSupport" label="timer"
                    :value="settings._timer" @change="setOption('_timer', $event)"></v-select>
                  <v-select v-model="settings.captureMode" :items="settings.captureModeSupport" label="mode"
                    :value="settings.captureMode" @change="setOption('captureMode', $event)"></v-select>

                </v-col>
                <v-col cols="6">
                  <v-select v-model="settings._angle" :items="settings._angleSupport" label="angle"
                    :value="settings._angle" @change="setOption('_angle', $event)"></v-select>
                  <v-select v-model="settings._cameraId" :items="settings._cameraIdSupport" label="camera"
                    :value="settings._cameraId" @change="setOption('_cameraId', $event)"></v-select>
                  <v-select v-model="settings._sceneMode" :items="settings._sceneModeSupport" label="scene"
                    :value="settings._sceneMode" @change="setOption('_sceneMode', $event)"></v-select>
                </v-col>
                <v-col cols="12">


                  <v-slider v-model="settings.captureInterval" hint="Capture Interval" max="90" min="2"
                    thumb-label="always" ticks @change="setOption('captureInterval', $event)"
                    label="interval"></v-slider>

                </v-col>


              </v-row>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" text @click="dialog = false">
                Close
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </div>
    </template>

  </div>
</template>

<script>
// import InfoPage from './InfoPage';

export default {
  components: {
    // InfoPage
  },


  data() {
    return {
      dialog: false,
      settings: {},
      CmdSettings: {
        name: "camera.getOptions",
        parameters: {
          optionNames: [
            "captureMode",
            "captureModeSupport",
            "exposureProgram",
            "exposureProgramSupport",
            "iso",
            "isoSupport",
            "shutterSpeed",
            "shutterSpeedSupport",
            "aperture",
            "apertureSupport",
            "whiteBalance",
            "whiteBalanceSupport",
            "exposureCompensation",
            "exposureCompensationSupport",
            "fileFormat",
            "fileFormatSupport",
            "speedOptimized",
            "previewFormat",
            "previewFormatSupport",
            "exposureDelay",
            "exposureDelaySupport",
            "captureInterval",
            "captureIntervalSupport",
            "captureNumber",
            "captureNumberSupport",
            "sleepDelay",
            "sleepDelaySupport",
            "totalSpace",
            "remainingSpace",
            "remainingPictures",
            "remainingVideoMinutes",
            "gpsInfo",
            "dateTimeZone",
            "hdr",
            "hdrSupport",
            "exposureBracket",
            "exposureBracketSupport",
            "gyro",
            "gyroSupport",
            "gps",
            "gpsSupport",
            "imageStabilization",
            "imageStabilizationSupport",
            "wifiPassword",
            "delayProcessing",
            "delayProcessingSupport",
            "_sceneMode",
            "_sceneModeSupport",
            "_timer",
            "_timerSupport",
            "_storage",
            "_batteryLevel",
            "_angle",
            "_angleSupport",
            "_audioChannel",
            "_audioChannelSupport",
            "_cameraId",
            "_cameraIdSupport",
            "_lgISO",
            "_lgISOSupport",
            "_lgShutterSpeed",
            "_lgShutterSpeedSupport",
            "_lgWhiteBalance",
            "_lgWhiteBalanceSupport",
          ],
        },
      },
    };
  },
  methods: {
    capture() {
      this.setOption('captureMode', "interval")
      this.postData(
          "http://192.168.43.1:6624/osc/commands/execute",
          { "name": "camera.startCapture" }
        )
        .then(response => {response.error ? this.log(response.error.message):this.log(response.state)})
    },
    record() {
      this.setOption('captureMode', "video")
        this.postData(
          "http://192.168.43.1:6624/osc/commands/execute",
          { "name": "camera.startCapture" }
        )
        .then(response => {response.error ? this.log(response.error.message):this.log(response.state)})
    },
    takePicture() {
      this.postData(
        "http://192.168.43.1:6624/osc/commands/execute",
        { "name": "camera.takePicture" }
      )
      .then(response => {response.error ? this.log(response.error.message):this.log(response.state)})

      
    },
    log(message) {
      this.$emit("log", message);
      // console.log(message);
    },
     getStatus(id){
       return this.postData(
          "http://192.168.43.1:6624/osc/commands/status",{"id":id}
        )


    },
    getAllOptions() {
      if (window.test) {
        this.settings = window.sampleSettings.results.options;
        console.log(this.settings);
      } else {
        this.postData(
          "http://192.168.43.1:6624/osc/commands/execute",
          this.CmdSettings
        )
          .then((data) => {
            this.settings = data.results.options;
          })
      }
    },
     setOption(parameter, value) {
      return this.postData("http://192.168.43.1:6624/osc/commands/execute", {
        name: "camera.setOptions",
        parameters: {
          options: {
            [parameter]: value,
          },
        },
      }).then(this.log(parameter +": "+value ));
    },

     postData(url, data = {}) {
      return fetch(url, {
    method: 'POST', // *GET, POST, PUT, DELETE, etc.
    headers: {
      'Content-Type': 'application/json;charset=utf-8',
      'Cache-Control': 'no-cache',
      // 'Content-Type': 'application/x-www-form-urlencoded',
    },
    body: JSON.stringify(data) // body data type must match "Content-Type" header
  })      
      .then(response => response.json())
      .catch(err=>this.log(err))
    },
  },
  mounted() {
    setTimeout(() => {
      this.getAllOptions();
      }, 100);
 
  
  },
  watch: {},
};
</script>